	module.exports = {"data":{"talkJson":{"description":"\"The Mother of All Demos is a name given retrospectively to Douglas Engelbart's December 9, 1968, demonstration of experimental computer technologies that are now commonplace. The live demonstration featured the introduction of the computer mouse, video conferencing, teleconferencing, hypertext, word processing, hypermedia, object addressing and dynamic file linking, bootstrapping, and a collaborative real-time editor.\"","meta":{"duration":"PT1H40M53S"},"presenter":{"name":"Douglas Engelbart"},"slug":"the-mother-of-all-demos","source":{"id":"yJDv-zdhzMY","provider":"youtube"},"tag":["Stanford Research Institute","Augmentation Research Center","Educational"],"title":"The Mother of All Demos"},"allTalkJson":{"edges":[{"node":{"description":"Alan Kay talks about alternative approaches to teach Computer Science in schools.","meta":{"duration":"PT1H10M32S"},"presenter":{"name":"Alan Kay"},"source":{"id":"N9c7_8Gp7gI","provider":"youtube"},"slug":"rethinking-computer-science-education","tag":["Educational","CrossRoads 2015","Inspiring"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/N9c7_8Gp7gI/default.jpg","height":90,"width":120}},"title":"Rethinking Computer Science Education"}},{"node":{"description":"Drawing from some of the most pivotal points in his life, Steve Jobs, chief executive officer and co-founder of Apple Computer and of Pixar Animation Studios, urged graduates to pursue their dreams and see the opportunities in life's setbacks.","meta":{"duration":"PT15M5S"},"presenter":{"name":"Steve Jobs"},"source":{"id":"UF8uR6Z6KLc","provider":"youtube"},"slug":"steve-jobs-stanford-commencement-address","tag":["Stanford University","Educational","Keynote","Inspiring","Commencement Speech"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/UF8uR6Z6KLc/default.jpg","height":90,"width":120}},"title":"Steve Jobs' 2005 Stanford Commencement Address"}},{"node":{"description":"You and Your Research is the blueprint for a successful career in any discipline, not just research; in fact, the talk has the nickname “You and Your Career”. In this lecture, Richard Hamming shares his observations on “why do so few scientists make significant contributions and so many are forgotten in the long run?’’ Some of the key ideas include courage, luck, drive (“knowledge and productivity are like compound interest”), a focus on important problems (“If you do not work on an important problem, it’s unlikely you’ll do important work”), open doors, selling the work (“I suggest that when you open a journal, as you turn the pages, you ask why you read some articles and not others”), and much more. This should be required viewing for every high school student.","meta":{"duration":"PT44M3S"},"presenter":{"name":"Richard Hamming"},"source":{"id":"a1zDuOPkMSw","provider":"youtube"},"slug":"you-and-your-research","tag":["Inspiring","Career","Educational"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/a1zDuOPkMSw/default.jpg","height":90,"width":120}},"title":"You and Your Research"}},{"node":{"description":"Salman Khan talks about how and why he created the remarkable Khan Academy, a carefully structured series of educational videos offering complete curricula in math and, now, other subjects. He shows the power of interactive exercises, and calls for teachers to consider flipping the traditional classroom script -- give students video lectures to watch at home, and do \"homework\" in the classroom with the teacher available to help.","meta":{"duration":"PT20M28S"},"presenter":{"name":"Salman Khan"},"source":{"id":"nTFEUsudhfs","provider":"youtube"},"slug":"lets-use-video-to-reinvent-education","tag":["TEDTalks","Educational","Inspiring"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/nTFEUsudhfs/default.jpg","height":90,"width":120}},"title":"Let's use video to reinvent education"}}]}},"pathContext":{"slug":"the-mother-of-all-demos","tags":["Stanford Research Institute","Augmentation Research Center","Educational"]}}

